	export function getNinjas(){
		return ninjas
	}
	export function getNinja(id){
		return ninjas.find(n => n.id==id)
	}
	export function searchNinja(text){

		if(text==='all'){
			return ninjas
		}else{
			return ninjas.filter(n =>( n.name.includes(text)|| n.id.includes(text)) )
		}
		
		
	}
	

